"use client";
import React, { useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import { Shield, X, AlertCircle, CheckCircle2 } from 'lucide-react';

interface InsuranceModalProps {
    isOpen: boolean;
    onClose: () => void;
    onResponse: (hasPurchased: boolean) => void;
}

export default function InsuranceVerificationModal({ isOpen, onClose, onResponse }: InsuranceModalProps) {
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
        if (isOpen) {
            document.body.style.overflow = 'hidden';
        } else {
            document.body.style.overflow = 'unset';
        }
        return () => {
            document.body.style.overflow = 'unset';
        };
    }, [isOpen]);

    if (!mounted || !isOpen) return null;

    return createPortal(
        <div className="fixed inset-0 z-[9999] flex items-center justify-center p-4">
            {/* Backdrop with Blur */}
            <div
                className="absolute inset-0 bg-black/60 backdrop-blur-xl transition-opacity animate-in fade-in duration-300"
                onClick={onClose}
            ></div>

            {/* Modal Content */}
            <div className="relative w-full max-w-lg bg-[#0A0A0A] border border-white/10 rounded-3xl shadow-2xl p-8 transform transition-all animate-in zoom-in-95 fade-in duration-300 overflow-hidden">
                {/* Decorative Elements */}
                <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-amber-500/50 via-amber-400 to-amber-500/50"></div>
                <div className="absolute top-0 right-0 p-4">
                    <button
                        onClick={onClose}
                        className="text-white/40 hover:text-white transition-colors p-2 hover:bg-white/5 rounded-full"
                    >
                        <X size={20} />
                    </button>
                </div>

                <div className="flex flex-col items-center text-center">
                    {/* Icon */}
                    <div className="w-20 h-20 bg-amber-500/10 rounded-full flex items-center justify-center mb-6 border border-amber-500/20 shadow-[0_0_40px_-10px_rgba(245,158,11,0.3)]">
                        <Shield className="text-amber-400" size={40} strokeWidth={2} />
                    </div>

                    <h3 className="text-2xl font-bold text-white mb-3 tracking-tight">
                        Asset Protection Gate
                    </h3>

                    <p className="text-secondary text-lg mb-8 leading-relaxed max-w-sm">
                        Intellectual property protocols require active coverage.
                        <span className="block mt-4 text-white/90 font-medium">
                            Do you currently hold a verified $99 Business Insurance Plan?
                        </span>
                    </p>

                    <div className="grid grid-cols-1 gap-4 w-full">
                        <button
                            onClick={() => onResponse(false)}
                            className="group relative w-full py-4 px-6 bg-gradient-to-r from-blue-600 to-indigo-600 hover:from-blue-500 hover:to-indigo-500 text-white font-bold rounded-xl transition-all shadow-lg shadow-blue-500/20 hover:shadow-blue-500/30 hover:scale-[1.02] active:scale-[0.98] flex items-center justify-center gap-3"
                        >
                            <span>No, Purchase Insurance Plan</span>
                            <div className="w-6 h-6 bg-white/20 rounded-full flex items-center justify-center group-hover:bg-white/30 transition-colors">
                                <AlertCircle size={14} />
                            </div>
                        </button>

                        <button
                            onClick={() => onResponse(true)}
                            className="w-full py-4 px-6 bg-white/5 hover:bg-white/10 border border-white/10 hover:border-white/20 rounded-xl font-bold text-white transition-all hover:scale-[1.02] active:scale-[0.98] flex items-center justify-center gap-3"
                        >
                            <span>Yes, I Have Coverage</span>
                            <CheckCircle2 size={18} className="text-green-500" />
                        </button>
                    </div>

                    <div className="mt-6 flex items-center gap-2 text-xs text-secondary/60">
                        <Shield size={12} />
                        <span>Verification secured by Verstige SSL</span>
                    </div>
                </div>
            </div>
        </div>,
        document.body
    );
}
